.footer{
    padding: 70px 0px;
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-bottom: 35px;
        border-bottom: 2px solid #454545;
        .logo{
            width: 150px;
            display: block;
            img{
                width: 100%;
            }
        }
        ul{
            display: flex;
            align-items: center;
            margin-bottom: 35px;
            li{
                &:not(:last-child){
                    margin-right: 38px;
                }

                a{
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 19px;
                    color: #454545;
                    &:hover{
                        opacity: 0.8;
                    }
                }
            }
        }

        .address, .phone{
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #666666;
        }

        .address{
            margin-bottom: 10px;
        }

        
    }

    &__bottom{
        padding-top: 20px;
        a{
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #666666;
        }
    }
}

@media(max-width: 768px){
    .footer{
        padding: 60px 0px;
        &__top{
            flex-direction: column-reverse;
            .logo{
                margin-bottom: 30px;
            }
            ul{
                flex-direction: column;
               margin-bottom: 0;
                li{
                    margin-right: 0 !important;
                    margin-bottom: 17px;
                }
            }

            .address{
                text-align: center;
                max-width: 250px;
            }

            .phone{
                text-align: center;
                display: block;
                margin: 0 auto;
                margin-top: 15px;
            }
        }

        &__bottom{
            text-align: center;
        }
    }
}