


.header{
    background: rgba(0, 0, 0, 0.75);
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 99;
    &-homepage{
      background: rgba(0, 0, 0, 0.22);
    }
    &__inner{
        display: flex;
        align-items: center;
        padding: 30px 0px;
    }

    &__logo{
        margin-right: 80px;
        max-width: 120px;
        img{
            width: 100%;
        }
    }

    &__menu{
        display: flex;
        align-items: center;
        padding-bottom: 20px;
        border-bottom: 2px #fff solid;
        position: relative;
        li{
            &:not(:last-child){
                padding-right: 30px;
            }
            a{
                font-weight: 400;
                font-size: 16px;
                color: #FFFFFF;
            }
           
        }
       
    }

    .cart{
        margin-left: auto;
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        color: #454545;
        padding: 5.5px 16.5px;
        background: #FECC00;
        border-radius: 49px;
        &:hover{
            opacity: 0.9;
        }
    }

    .float-line{
        position: absolute;
        bottom: -2px;
        height: 2px;
        background: #FECC00;
        
    }


    .ico-btn{
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
        padding: 7px;
        background: #FECC00;
        cursor: pointer;
        transition: all .20s ease-in;
        border-radius: 50%;
        margin-left: 10px;
      }
      
      .ico-btn:hover{
        
        opacity: 0.8;
      }
      
      .ico-btn__burger,
      .ico-btn__burger::before,
      .ico-btn__burger::after{
        width: 16px;
        height: 2px;
        background: currentColor;
        border-radius: 2px;
        transition: all .25s cubic-bezier(0.4, 0.0, 0.2, 1);;
      }
      
      .ico-btn__burger::before{
        content: '';
        position: absolute;
        transform: translateY(-6px);
      }
      
      .ico-btn__burger::after{
        content: '';
        position: absolute;
        transform: translateY(6px);
      }
      
      /* -------------------------------------------
      **  ANIMATION 
      ============================================= */
      .ico-btn.is-active .ico-btn__burger{
        background: transparent;
      }
      
      .ico-btn.is-active .ico-btn__burger::before{
        transform: rotate(-45deg);
      }
      
      .ico-btn.is-active .ico-btn__burger::after{
        transform: rotate(45deg);
      }
      
      .ico-btn.is-active{
        transform: rotate(360deg);
      }

      
}



