@import 'fonts'; // Шрифты
@import 'reset'; // Сброс стилей
@import 'setting'; // Общие настройки
@import 'vars'; // Переменные
@import 'mixins'; // Миксины


// Компоненты 
@import 'components/footer';
@import 'components/header';
  

*{
    transition: 0.2s linear;
}

// Стили




.main-screen{
    min-height: 720px;
    background: #3F2E9A;
    padding-top: 120px;
    position: relative;
    &__inner{
        position: relative;
        &::before{
            content: '';
            background-image: url('../img/ARTCOLLECTION.svg');
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            position: absolute;
            width: 100vw;
            height: 350px;
            z-index: 5;
            left: 50%;
            transform: translateX(-50%);
            top: 90px;
        }

        &::after{
            content: '';
            background-image: url('../img/bottles.png');
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            max-width: 1040px;
            height: 865px;
            width: 100%;
            top: -122px;
            right: -300px;
            position: absolute;
            z-index: 9;
        }
    }
    &::before{
        content: '';
        background: url('../img/pattern.svg');
        width: 100%;
        background-position: center;
        background-repeat:repeat-x;
        position: absolute;
        bottom: 0;
        left: 0;
        height: 75px;
        z-index: 5;
    }
    

    h1{
        padding-top: 140px;
        font-weight: 700;
        font-size: 40px;
        line-height: 47px;
        color: #FFFFFF;
        margin-bottom: 70px;
        max-width: 470px;
        position: relative;
        z-index: 10;
    }

    .big-btn{
        font-weight: 700;
        font-size: 24px;
        line-height: 28px;
        color: #454545;
        padding: 16px 30px;
        background: #FECC00;
        border-radius: 49px;
        position: relative;
        z-index: 10;
        &:hover{
            opacity: 0.8;
        }
    }
}


.about{
    &__inner{
        padding: 100px 0px;
        display: flex;
    }
    &__img{
        max-width: 400px;
        width: 100%;
        margin-right: 9%;
        max-height: 100%;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__content{
        &-logo{
            svg{
                max-width: 95px;
                width: 100%;
            }
        }
        &-title{
            font-weight: 700;
            font-size: 30px;
            line-height: 35px;
            color: #454545;
            margin-bottom: 20px;
            margin-top: 15px;
        }

        &-text{
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #666666;
            max-width: 600px;
        }
    }
}

.title-wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    .title{
        font-weight: 700;
        font-size: 30px;
        line-height: 35px;
        color: #454545;
    }
    padding-bottom: 15px;
    border-bottom: 2px solid #454545;
    margin-bottom: 70px;
}


.products{
    padding-top: 40px;
    .ionTabs__head{
        display: flex;
        align-items: center;
        li{
            font-weight: 700;
            font-size: 24px;
            line-height: 28px;
            color: #454545;
            margin-left: 50px;
            position: relative;
            &::after{
                content: '';
                width: 100%;
                position: absolute;
                bottom: -18px;
                height: 2px;
                background: #FECC00;
                opacity: 0;
                left: 50%;
                transform: translateX(-50%);
            }
        }
        .ionTabs__tab_state_active{
            color: rgba(69, 69, 69, 0.55);
            &::after{
                opacity: 1;
            }
        }
    }


    &-grid{
        display: grid !important;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 20px;
        .product{
            &-item{
                &:hover{
                    .product-imgs{
                        .main-image{
                            opacity: 0;
                        }
                        .hover-image{
                           opacity: 1;
                        }
                    }
                }

                .more-btn{
                    font-weight: 700;
                    font-size: 16px;
                    line-height: 19px;
                    color: #302D42;
                    width: 100%;
                    padding: 15px 50px;
                    background: #FECC00;
                    border-radius: 50px;
                    display: block;
                    text-align: center;
                    max-width: 240px;
                    &:hover{
                        opacity: 0.8;
                    }
                }

            }

            &-imgs{
                max-width: 100%;
                height: 460px;
                overflow: hidden;
                position: relative;
                background: #F9F9F9;
                img{
                    position: absolute;
                    left: 0;
                    top: 0;
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }

                .hover-image{
                   opacity: 0;
                }

                
            }

            &-name{
                font-weight: 700;
                font-size: 24px;
                line-height: 28px;
                color: #454545;
                margin-top: 40px;
                margin-bottom: 10px;
            }
            &-descr{
                font-weight: 400;
                font-size: 16px;
                line-height: 19px;
                color: #666666;
                max-width: 225px;
                margin-bottom: 30px;
            }
           
        }
    }

    .all-product{
        font-weight: 700;
        font-size: 24px;
        line-height: 28px;
        text-align: center;
        color: #454545;
        position: relative;
        margin: 0 auto;
        display: block;
        width: max-content;
        margin-top: 70px;
        &:hover{
            &::after{
               opacity: 0;
            }
        }
        &::after{
            content: '';
            width: 100%;
            height: 2px;
            background: #454545;
            bottom: -3px;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
        }
    }
}


.btn{
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    color: #454545;
    padding: 15px 56px;
    background: #FECC00;
    border-radius: 50px;
    &:hover{
        opacity: 0.8;
    }
}
.documents{
    margin-top: 100px;
    padding: 140px 0px;
    background: #FBFBFB;
    
    &__slider{
        width: 100%;
        overflow: hidden;
        &-wrapper{
            display: flex;
            width: 100%;
            height: 100%;
        }
    }
    &-slide{
        flex-shrink: 0;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__descr{
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #666666;
        margin-top: 45px;
        margin-bottom: 55px;
    }

    .swiper-pagination{
        margin-top: 15px;
        position: static;
        .swiper-pagination-bullet{
            opacity: 1 !important;
            background: #DCDCDC;
        }
        .swiper-pagination-bullet-active{
            background: #fecc00;
            opacity: 1 !important;
        }
    }
   
}


.contact{
    margin-top: 120px;
    .map{
        max-width: 100%;
        width: 100%;
        height: auto;
        max-height: 500px;
        max-width: 1600px;
        margin: 0 auto;
        &-wrapper{
            background: #F9F9F9;
        }
        img{
            width: 100%;
            height: auto;
            max-height: 500px;
            object-fit: cover;
        }

        .map-mobile{
            display: none;
        }

    }
}



.archive-products{
    margin-top: 190px;
    padding-bottom: 50px;
}


input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button{
    -webkit-appearance: none;
}

input[type=number] {
    -moz-appearance: textfield;
  }

.single-product{
    padding-top: 190px;
    .product-card{
        &__inner{
            display: flex;
        }
        &__img{
            max-width: 280px;
            width: 100%;
            margin-right: 90px;
        }

        &__info{
            width: 100%;
            .product{
                &-name{
                    font-weight: 700;
                    font-size: 30px;
                    line-height: 35px;
                    color: #454545;
                }

                &-descr{
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 24px;
                    color: #666666;
                    margin: 30px 0px;
                }

                &-counters{
                    max-width: 600px;
                    margin-bottom: 40px;
                 
                }
            }
        }
    }
}

.counter-wrapper{
    width: 100%;
    padding: 12px 24px;
    display: flex;
    justify-content: space-between;
    .counter-name{
        font-weight: 500;
        font-size: 18px;
        line-height: 147.19%;
        color: #454545;
    }
    &:nth-child(odd){
        background: #FBFBFB;
    }

    .counter{
        display: flex;
        input{                             
            max-width: 100px;
            font-weight: 500;
            font-size: 18px;
            line-height: 147.19%;
            text-align: center;
            color: #666666;
            border: 1px solid #D6D6D6;
            border-radius: 50px;
            margin: 0px 7px;
        }
        button{
            width: 32px;
            height: 32px;
            border: 1px solid #D6D6D6;
            border-radius: 100%;
            padding: 0;
            font-weight: 500;
            font-size: 18px;
            color: #666666;
            padding-left: 1px;
            cursor: pointer;
            &:hover{
                opacity: 0.7;
            }

            &:disabled{
                opacity: 0.4;
                cursor: default;
            }
        }
    }
}


.cart-page{
    padding-top: 190px;
    h1{
        font-weight: 700;
        font-size: 30px;
        line-height: 35px;
        color: #454545;
    }
    .cart-items{
        margin-top: 50px;
    }

    .btn-wrapper{
        display: flex;
        justify-content: flex-end;
        margin-top: 40px;
    }
}


.error{
    padding-top: 190px;
    &_inner{
        display: flex;
        justify-content: space-between;
        img{
            max-width: 60%;
            height: auto;
        }
        .content{
            max-width: 480px;
            width: 100%;
           
           span{
               display: block;
           }

           .big{
            font-weight: 700;
            font-size: 128px;
            line-height: 150px;
            color: #454545;
           }

          
           .default{
                font-weight: 700;
                font-size: 30px;
                line-height: 35px;
                color: #454545;
                margin: 30px 0px;
           }

           .small{
            font-weight: 500;
            font-size: 20px;
            line-height: 147.19%;
            color: #666666;
            margin-bottom: 40px;
           }
        }
    }
}

@import 'media';  // Должен быть в конце проекта