@media(min-width: 960px){
    .ico-btn{
        display: none !important;
    }
}

@media(max-width: 960px){
    .products .ionTabs__head li{
        font-size: 20px;
        margin-left: 30px;
    }
    .title-wrapper{
        .title{
            font-size: 25px;
        }
    }
    .about__inner{
        padding-bottom: 50px;
    }
    .header__menu{
        position: absolute;
        flex-direction: column;
        background: #FFFFFF;
        align-items: flex-start;
        box-shadow: 0px 4px 8px 4px rgba(0, 0, 0, 0.03);
        right: 40px;
        top: 85px;
        padding: 20px;
        min-width: 175px;
        opacity: 0;
        visibility: hidden;
        .float-line{
            display: none;
        }
        li{
            margin-bottom: 12px;
            padding-right: 0 !important;
            padding-bottom: 5px;
           
            a{
                font-weight: 400;
                color: #454545;
            }
        }

        li.active{
            border-bottom: 2px solid #FECC00;
        }
    }

    .header__menu.open{
        opacity: 1;
        visibility: visible;
    }


    body{

        .single-product .product-card__img{
            margin-right: 25px;
        }
        .single-product .product-card__info .product-name{
            font-size: 25px;
        }
       
        .main-screen{
            min-height: 63vw;
            &::before{
                height: 45px;
                background-size: cover;
            }
            h1{
                font-size: 30px;
                margin-bottom: 40px;
                padding-top: 10%;
            }
            &__inner{
                &::after{
                    max-width: 70vw;
                    height: 70vw;
                    right: 0;
                }
                &::before{
                    height: 30vw;
                    top: 50px;
                }
            }

            .big-btn{
                font-size: 18px;
                padding: 19px 47px;
            }
        }


        .about{
            &__img{
                max-width: 345px;
                max-height: 100%;
                width: 100%;
                margin-right: 35px;
                img{
                    max-width: 345px;
                    min-height: 100%;
                    width: 100%;
                    max-height: 100%;
                }
            }
            &__content{
                &-title{
                    font-size: 25px;
                    margin: 15px 0px;
                }
                &-text{
                    font-size: 16px;
                }
            }
        }

        .products-grid{
            grid-template-columns: repeat(3, 1fr);
            .product-name{
                font-size: 18px;
                margin-top: 30px;
                margin-bottom: 15px;
            }
            .product-descr{
                font-size: 16px;
            }
        }

        .products .all-product{
            font-size: 18px;
        }
    }
}


@media(max-width: 768px){
    .error{
        &_inner{
            flex-direction: column;
            align-items: center;
            text-align: center;
            img{
                margin-top: 150px;
                max-width: 100%;
            }

            .content{
                .big{
                
                    font-size: 96px;
                    line-height: 112px;
                   }
        
                  
                .default{
                    
                    font-size: 20px;
                    margin: 15px 0px;
                }
    
                .small{
              
                font-size: 16px;
               
                }
            }
           
        }
    }
    .contact{
        margin-top: 60px;
        .map-wrapper{
            .map{
                &-desktop{
                    display: none;
                }

                &-mobile{
                    display: block;
                }
            }
        }
    }
    .documents{
        margin-top: 80px;
        padding: 80px 0px;
        background: #FBFBFB;

    

        &__descr{
            margin-top: 30px;
            margin-bottom: 35px;
        }

        &__grid{
        margin-top: -20px;

        }

    }
    
    body{

        .cart-page .counter-wrapper .counter{
            margin: 0 auto;
            margin-top: 15px;
        }
        .cart-page .counter-wrapper{
            flex-direction: column;
            padding: 10px;
        }
        .single-product .product-card__info .product-counters .counter-wrapper{
            align-items: center;
            padding: 8px 10px;
        }
        .single-product .product-card__info .product-counters .counter-wrapper .counter input{
            max-width: 52px;
        }
        .single-product .product-card__inner{
            flex-direction: column;
        }
        .single-product .product-card__img{
            margin: 0 auto;
            margin-bottom: 50px;
        }
        .single-product .product-card__info .product-counters{
            max-width: 100%;
        }
        .single-product .product-card__info .product-descr{
            font-size: 16px;
            margin: 25px 0px;
        }
        .single-product .product-card__info .product-counters .counter-wrapper .counter-name{
            font-size: 16px;
        }
        .products-grid{
            grid-template-columns: repeat(2, 1fr);
            justify-content: center;
            .product-descr{
                margin-bottom: 15px;
            }
        }
        .header__logo{
            max-width: 107px;
            margin-right: 0;
        }
        .main-screen{
            min-height: 550px;
            &::before{
                height: 35px;
            }
            h1{
                font-size: 24px;
                margin-bottom: 45px;
                padding-top: 60px;
                max-width: 300px;
                line-height: 28px;
            }
            &__inner{
                &::after{
                    max-width: 300px;
                    height: 300px;
                    left: -58px;
                    top: 193px;
                }
                &::before{
                    height: 250px;
                    top: 50px;
                }
            }

            .big-btn{
                font-size: 18px;
                padding: 19px 17px;
            }
        }

        .about{
            &__inner{
                flex-direction: column-reverse;
            }
            &__img{
                max-width: 100%;
                max-height: 100%;
                height: auto;
                margin-top: 40px;
                img{
                    max-width: 100%;
                    max-height: 100%;
                    height: auto;
                }
            }
            
        }

        .title-wrapper{
            margin-bottom: 40px;
        }

    }

    .archive-products{
        margin-top: 100px;
    }
}

@media(max-width: 540px){

  
    .title-wrapper{
        flex-direction: column;
        align-items: flex-start;
        border-bottom: 0;
        padding-bottom: 0;
       .title{
           padding-bottom: 15px;
           margin-bottom: 15px;
           display: block;
           width: 100%;
           border-bottom: 2px solid #454545;
       }
       
    }
    .products .ionTabs__head li{
        margin-left: 0;
        margin-right: 25px;
    }

    .products .ionTabs__head .ionTabs__tab_state_active{
        &::after{
            top: -17px ;
            bottom: auto ;
        }
        
    }
}


@media(max-width: 480px){
    body{
        .products-grid{
            grid-template-columns: 1fr;
        }
        .product-home .products-grid{
            .product-item:not(:first-child){
                display: none;
            }
        }
        
    }

}